body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.row {
  display: -ms-flexbox;
  display: flex;

}

ul {
  padding: 0;
}

h2 {
}

.cell {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 50px;
  width: 50px;
  border: 2px solid black;
  background-color: aqua;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.cell * {
  font-weight: bold;
}

.cell:hover {
  border: 2px solid #32cd32;
}

.hitMark {
  display: none;
  color: red;
}

.shadowMark{
  font-size: 30px;
  color: gray;
  display: none;
}

.missMark {
  display: none;
}

.cell.hit .hitMark {
  display: inline;
}

.cell.miss .missMark {
  display: inline;
}

.cell.shadow .shadowMark {
  display: inline;
}

.board {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 50px 0 50px;
}

.boards {
  display: -ms-flexbox;
  display: flex;

}



